import { Component, h, Prop, Element } from '@stencil/core';
import { TIcon } from '../../../components';

@Component({
  tag: 'dc-ui-meta-nav-button',
  styleUrl: 'dc-ui-meta-nav-button.scss',
  shadow: true,
})
export class DcUiMetaNavButton {
  @Element() el: HTMLElement;

  @Prop() label: string;
  @Prop() icon: TIcon;
  @Prop() badge?: boolean;
  @Prop() selected?: boolean;
  @Prop() language?: boolean = false;
  @Prop() disabled?: boolean = false;

  render() {
    const classes = {
      'meta-nav-button': true,
      'meta-nav-button--selected': this.selected,
      'meta-nav-button--badge': this.badge,
      'meta-nav-button--disabled': this.disabled,
    };

    return (
      <button
        disabled={this.disabled}
        class={classes}
        aria-label={this.label}>
        <dc-ui-icon name={this.icon} />
        <span>{this.label}</span>
      </button>
    );
  }
}
